iers();\n            if(result) {\n                this.dataForPopupTips.show = true;\n                this.dataForPopupTips.title = '输入有误';\n                this.dataForPopupTips.content = result;\n                this.buttonEnabled = true;\n                return;\n            }\n            var url = '/api/v2/admin/suppliers/edit?id=' + this.data.supplierId;\n            var data = {\n                name: this.data.name,\n                product: this.data.product,\n                region: this.data.region,\n                address: this.data.address,\n                contact: this.data.contact,\n            };\n            this.$http.put(url, data)\n            .then((response) => {\n                if(!response.data.result || response.data.result === 0) {\n                    this.show = false;\n                    this.$dispatch('edit-supplier');\n                } else {\n                    callback.success(response.data);\n                }\n                this.buttonEnabled = true;\n            },(response) => {\n                callback.error(response);\n                this.buttonEnabled = true;\n            });\n        },\n    \tcloseDialog() {\n            this.show = false;\n        },\n    }\n}\n</script>\n\n<style scoped>\n.modal {\n    height: 100%;\n}\n\n.modal-dialog {\n    top: 20%;\n}\n\n</style>"],"sourceRoot":"webpack://"}]);

